.sb-checkbox-field {
  --sb-field-width: fit-content;
}

/*
Checkboxes component uses Children.map to render children which makes hard to ignore empty elements.
This is a workaround to hide empty elements and style them in read only mode.
 */
.sb-checkbox-field_read-only {
  gap: var(--sbt-spacing-20);

  .sb-checkboxes__list-item {
    margin: 0;
  }

  .sb-checkboxes__list-item:empty {
    display: none;
  }
}
