// SB Multi Input Date Range Input Variables:
// --sb-daterange-input-inline-margin-right - margin between start and end date for inline orientation
// --sb-daterange-input-stacked-margin-bottom - margin between start and end date for stacked orientation

.sb-daterange-input {
  display: flex;
  padding: 1rem;
}

.sb-daterange-input_inline {
  flex-direction: row;

  .sb-date-input:first-child {
    margin-right: var(--sb-daterange-input-inline-margin-right, 1rem);
  }
}

.sb-daterange-input_stacked {
  flex-direction: column;

  .sb-date-input {
    margin-bottom: var(--sb-daterange-input-inline-margin-bottom, 1rem);
  }
}

/* stylelint-disable selector-class-pattern -- Third Party Classes */
.daterange-input {
  .react-datepicker__day {
    &:hover {
      background-color: var(--sbt-color-white);
      border-color: var(--sbt-theme-color-50);
      border-radius: 50%;
      color: #000;
    }

    // clicked day, similar to date input
    &:active {
      background-color: var(--sbt-theme-color-80);
      border-color: var(--sbt-theme-color-80);
      color: var(--sbt-white);
    }
  }

  //don't show gray background when selecting a new range inside the current range
  .react-datepicker__day--in-range:not(
      .react-datepicker__day--in-selecing-range
    ) {
    background-color: var(--sbt-color-white);
  }

  //reduce margins so background colors blend together
  .react-datepicker__day,
  .react-datepicker__day-name {
    margin-left: 0;
    margin-right: 0;
  }

  //show circle when start or end date has not been selected yet
  .react-datepicker__day--in-selecting-range.react-datepicker__day--selecting-range-start.react-datepicker__day--selecting-range-end {
    border-radius: 50%;
  }

  // show range start as semi-circle
  .react-datepicker__day--in-selecting-range.react-datepicker__day--selecting-range-start {
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 0;
    border-top-left-radius: 50%;
    border-top-right-radius: 0;
  }

  // show range end as semi-circle
  .react-datepicker__day--in-selecting-range.react-datepicker__day--selecting-range-end {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 50%;
    border-top-left-radius: 0;
    border-top-right-radius: 50%;
  }

  .react-datepicker__day--in-selecting-range {
    border-radius: 0;
    color: #000;

    // lighten color for cells except for start and end
    &:not(.react-datepicker__day--selecting-range-start):not(
        .react-datepicker__day--selecting-range-end
      ) {
      background-color: var(--sbt-theme-color-10);
    }

    &:active {
      background-color: var(--sbt-theme-color-80);
      border-color: var(--sbt-theme-color-80);
      color: var(--sbt-white);
    }
  }

  //set colors for start and end range
  .react-datepicker__day--selecting-range-start,
  .react-datepicker__day--selecting-range-end,
  .react-datepicker__day--range-start.react-datepicker__day--selecting-range-start,
  .react-datepicker__day--range-end.react-datepicker__day--selecting-range-end {
    background-color: var(--sbt-theme-color-50);
    border-color: var(--sbt-theme-color-50);
    color: var(--sbt-white);
  }

  // set colors for start and end range when new range is outside current range
  .react-datepicker__day--selected.react-datepicker__day--in-range.react-datepicker__day--range-start,
  .react-datepicker__day--selected.react-datepicker__day--in-range.react-datepicker__day--range-end {
    background-color: var(--sbt-theme-color-50);
    color: var(--sbt-white);
  }
}
/* stylelint-enable selector-class-pattern -- Third Party Classes */
