@import '../../../styles/breakpoints';

.sb-form-generator_section {
  background: var(--sb-form-generator-section-background-color);
  margin: var(--sb-form-generator-section-margin, 20px) 0;
  padding: var(--sbt-spacing-30);
}

.sb-form-generator_section--collapsible {
  --sb-collapsible-panel-rotation: 180deg;
  padding-bottom: 0;

  @include window-width-min-slim {
    --sb-collapsible-panel-title-border: solid 1px
      var(--sbt-theme-tertiary-color-20);
  }

  &-icon {
    line-height: var(
      --sb-typography-T40-line-height,
      var(--sbt-line-height-80)
    );
  }

  .sb-collapsible-panel__title {
    margin-bottom: var(
      --sb-form-generator-header-margin,
      var(--sbt-spacing-10)
    );
    padding-right: 0;
  }

  .sb-collapsible-panel__content-inner {
    padding-left: 0;
    padding-right: 0;
  }
}

.sb-form-generator_section-label {
  display: flex;
  font-size: var(
    --sb-form-generator-section-header-size,
    var(--sbt-font-size-40)
  );
}

.sb-form-generator_section .sb-form-generator_section {
  margin: 0;
}

.sb-form-generator_section-description {
  color: var(--sbt-theme-tertiary-color-60);
  font-size: var(
    --sb-form-generator-section-description-size,
    var(--sbt-font-size-25)
  );
}

.sb-form-generator_section-header {
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-20);
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--sb-form-generator-header-margin, var(--sbt-spacing-10));
  padding-bottom: var(
    --sb-form-generator-header-spacing,
    var(--sbt-spacing-40)
  );
  width: 100%;

  &--collapsible {
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 0;
  }
}

.sb-form-generator_grid-breakpoint_slim {
  .sb-form-generator_section {
    border: 1px solid var(--sbt-theme-tertiary-color-20);
    border-radius: var(--sbt-border-radius-large);
    padding: var(
      --sb-form-generator-section-padding_slim,
      var(--sbt-spacing-40)
    );
  }

  .sb-form-generator_section--collapsible {
    border: none;

    .sb-collapsible-panel__title {
      padding: 0;
    }

    .sb-collapsible-panel__content-inner {
      padding-right: 0;
    }
  }

  .sb-form-generator_section-header {
    border-bottom: 0;
    padding-bottom: var(--sbt-spacing-10);
  }
}
