.sb-form-generator_pagination {
  height: var(--sb-form-generator-pagination-height, auto);
  margin-bottom: var(--sb-form-generator-pagination-margins, 20px);
}

.sb-form-generator_pagination-list,
.sb-form-generator_pagination-list-item {
  --sb-tabs-color-background: transparent;
  height: 100%;
}

.sb-form-generator_pagination-label {
  font-size: var(
    --sb-form-generator-pagination-font-size,
    var(--sbt-font-size-30)
  );
  margin: 0 5px;
}

.sb-form-generator_pagination-link {
  margin: 0 5px;
}

.sb-form-generator_pagination-dirty-icon {
  color: var(--sbt-color-warning-50);
  font-size: var(--sbt-font-size-10);
  left: 4px;
  position: absolute;

  &::before {
    position: relative;
    top: -1px;
  }
}

.sb-form-generator_pagination-error-icon {
  color: var(--sbt-color-error-50);
  left: 2px;
  position: absolute;

  &::before {
    position: relative;
    top: -1px;
  }
}

.sb-form-generator_pagination-sticky {
  background-color: var(--sbt-white);
  box-shadow: 0 10px 16px -2px rgba(67, 86, 100, 0.08);
  position: sticky;
  top: auto;
  z-index: calc(var(--sbt-stack-modal) + 100);
}
