.sb-confirmation-modal__dialog {
  max-width: 30rem;
}

.sb-confirmation-modal__content {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 3rem;
  white-space: pre-wrap;
}

.sb-confirmation-modal-icon {
  color: var(--sbt-confirmation-modal-icon-color);
  font-size: var(--sbt-font-size-70);
  margin-right: 20px;
}

.sb-confirmation-modal-button {
  --sb-button-color: var(
    --sbt-confirmation-modal-button-color,
    var(--sbt-theme-color-70)
  );

  &:hover {
    --sb-button-color: var(
      --sbt-confirmation-modal-button-color-hover,
      var(--sbt-theme-color-80)
    );
  }
}

.sb-confirmation-modal-button + .sb-confirmation-modal-button {
  margin-left: 0.5rem;
}

.sb-confirmation-modal__success {
  --sbt-confirmation-modal-icon-color: var(--sbt-color-success-50);
  --sbt-confirmation-modal-button-color: var(--sbt-color-success-50);
  --sbt-confirmation-modal-button-color-hover: var(--sbt-green-70);
}

.sb-confirmation-modal__warning {
  --sbt-confirmation-modal-icon-color: var(--sbt-color-warning-50);
  --sbt-confirmation-modal-button-color: var(--sbt-color-warning-50);
  --sbt-confirmation-modal-button-color-hover: var(--sbt-orange-70);
}

.sb-confirmation-modal__error {
  --sbt-confirmation-modal-icon-color: var(--sbt-color-error-50);
  --sbt-confirmation-modal-button-color: var(--sbt-color-error-50);
  --sbt-confirmation-modal-button-color-hover: var(--sbt-red-70);
}
