.sb-file-uploader__size-large {
  width: 900px;
}

.sb-file-uploader__size-auto {
  width: 100%;
}

.sb-file-uploader__input-hidden {
  display: none;
}

.sb-file-uploader__dragging {
  &-text {
    color: var(--sbt-theme-color-70);
  }

  .sb-icon__size-small {
    height: 20px;
    margin-right: 10px;
  }

  .sb-file-uploader__upload {
    background-color: transparent;
    border-color: var(--sbt-theme-color-50);
  }
}

.sb-file-uploader__container {
  position: relative;
}

.sb-file-uploader__upload {
  align-items: center;
  background-color: #fff;
  border: 2px dotted var(--sbt-theme-tertiary-color-30);
  border-radius: var(--sbt-border-radius-small);
  display: flex;
  flex-direction: column;
  padding: var(--sbt-spacing-50);
}

.sb-file-uploader__has-files .sb-file-uploader__upload {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.sb-file-uploader__description {
  color: var(--sbt-theme-tertiary-color-50);
  padding-top: var(--sbt-spacing-50);
}

.sb-file-uploader__button-container {
  align-items: center;
  display: flex;
}

.sb-file-uploader__label {
  margin-left: 0.5rem;
}

.sb-file-uploader__dragging-container {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  padding: 12px 0;
  position: absolute;
  width: 100%;
}

.sb-file-uploader__text {
  margin-right: 12px;
}

.sb-file-uploader__upload--dragging {
  .sb-file-uploader__button-container,
  .sb-file-uploader__description {
    opacity: 0;
  }
}

.sb-file-uploader__read-only-empty-message,
.sb-file-uploader__read-only-single-file {
  align-items: center;
  display: flex;
  justify-content: center;
}
