@import '../../styles/mixins';

.sb-textarea {
  background-color: var(--sbt-color-background);
  border: 1px solid var(--sbt-theme-tertiary-color-30);
  border-radius: var(--sbt-border-radius-small);
  color: var(--sbt-theme-tertiary-color-90);
  font-size: var(--sbt-font-size-30);
  height: 5rem;
  line-height: var(--sbt-line-height-25);
  padding: 0.25rem 0.5rem;
  transition: all 80ms cubic-bezier(0.2, 0, 0.4, 0.9);

  &::placeholder {
    color: var(--sbt-theme-tertiary-color-30);
  }

  &:focus {
    border-color: transparent;
    box-shadow: var(--sbt-input-box-shadow);
    outline: none;
    outline-color: var(--sbt-theme-color-40);
  }

  &:disabled,
  &:read-only {
    @include disabled-shake();
    background-color: var(--sbt-color-disabled-background);
    border: 1px solid var(--sbt-color-disabled-border);
    box-shadow: none;
    color: var(--sbt-color-disabled-text);
    cursor: not-allowed;

    &::placeholder {
      color: var(--sbt-theme-tertiary-color-80);
    }

    &:hover {
      background-color: var(--sbt-theme-tertiary-color-10);
      border: 1px solid var(--sbt-theme-tertiary-color-30);
      color: var(--sbt-theme-tertiary-color-80);
    }
  }
}

.sb-textarea__size-small {
  width: 200px;
}

.sb-textarea__size-medium {
  width: 400px;
}

.sb-textarea__size-large {
  width: 600px;
}

.sb-textarea__size-auto {
  width: 100%;
}

.sb-textarea_error {
  @include error();
  background-position: top calc(0.375em + 0.1875rem) right
    calc(0.375em + 0.1875rem);
}

.sb-textarea__height-auto {
  height: auto;
}

.sb-textarea__characters {
  color: var(
    --sb-textarea__characters-color,
    var(--sbt-theme-tertiary-color-50)
  );
  margin-top: var(--sbt-spacing-20);
}
