@import '../../styles/breakpoints';

.sb-form-generator_form {
  --sb-table-font-size: var(--sbt-font-size-30);
  height: 100%;
}

.sb-form-generator_form .sb-grid {
  --sb-grid-margin: 0;
  --sb-grid-row-gutter: 0;
}

.sb-form-generator_header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @include window-width-max-small {
    flex-wrap: wrap;
  }
}

.sb-form-generator_header-sticky {
  background-color: var(--sbt-white);
  box-shadow: 0 10px 16px -2px rgb(67 86 100 / 8%);
  padding: var(--sbt-spacing-10);
  position: sticky;
  top: 0;
  z-index: calc(var(--sbt-stack-modal) + 100);
}

.sb-form-generator_body {
  margin: var(--sb-form-generator-body-margins, 0)
    var(--sb-form-generator-body-margin-horizontal, 0);
}

.sb-form-generator_footer-sticky {
  background-color: var(--sbt-white);
  bottom: 0;
  box-shadow: 10px -10px 16px -2px rgb(67 86 100 / 8%);
  padding: var(--sbt-spacing-10);
  position: sticky;
  z-index: calc(var(--sbt-stack-modal) + 100);
}
