@import '../../../styles/mixins';

.sb-form-generator_field_hidden {
  display: none;
}

// https://css-tricks.com/preventing-a-grid-blowout/
.sb-form-generator_field {
  min-width: 0;
}

.sb-form-generator_toggle,
.sb-form-generator_toggleswitch,
.sb-form-generator_number,
.sb-form-generator_checkbox,
.sb-form-generator_date,
.sb-form-generator_multiselect,
.sb-form-generator_multiselect-popout,
.sb-form-generator_select,
.sb-form-generator_text,
.sb-form-generator_markdown,
.sb-form-generator_textarea,
.sb-form-generator_readonly,
.sb-form-generator_inputmask,
.sb-radio-field {
  margin: var(--sb-form-generator-field-margins, var(--sbt-spacing-20))
    var(--sb-form-generator-field-gutters, var(--sbt-spacing-10));
}

.sb-form-generator_textarea-input {
  // so rows attribute handles height
  height: auto;
}

.sb-form_generator_select-inline .sb-select__menu,
.sb-form_generator_multiselect-inline .sb-multiselect__menu,
.sb-form_generator_multiselect-inline .sb-multiselectpopout__wrapper,
.sb-form_generator_multiselect-inline .sb-multiselectpopout__dropdown-menu {
  position: relative;
}

.sb-form-generator_markdown {
  textarea:focus {
    border-color: transparent;
    box-shadow: var(--sbt-input-box-shadow);
    outline: none;
    outline-color: var(--sbt-theme-color-40);
  }
}

.sb-form-generator_markdown_error {
  @include error();
  background-position: top calc(0.375em + 0.1875rem) right
    calc(0.375em + 0.1875rem);
  border: var(--sbt-stroke-thin-error) !important;
}
