.sb-file-list {
  border: 1px solid var(--sbt-theme-tertiary-color-30);
  margin: 0;

  .sb-file-list__action {
    margin: 0;
  }

  &__file-loading {
    background-color: var(--sbt-theme-color-30);
    border-color: var(--sbt-theme-color-70);
  }
}

.sb-file-list__file-name {
  align-items: center;
  display: flex;

  > p {
    color: var(--sbt-theme-tertiary-color-70);
    font-size: var(--sbt-font-size-25);
  }
}

.sb-file-list__file-icon {
  margin-right: 0.5rem;
}

.sb-file-list__file-meta {
  color: var(--sbt-theme-tertiary-color-50);
  font-size: 1rem;
  margin-top: 1rem;
  padding-left: 24px;

  &-error {
    padding-left: 0;
  }
}

.sb-file-list__file-error {
  background-color: var(--sbt-color-error-10);
  border-color: var(--sbt-color-error-50);
}

.sb-file-list__file-error-icon {
  color: var(--sbt-color-error-50);
  margin-right: var(--sbt-font-size-0);
}

.sb-file-list__file-error-message {
  color: var(--sbt-theme-tertiary-color-70);
  font-size: var(--sbt-font-size-25);
  font-weight: var(--sbt-font-weight-70);
}

.sb-file-list__container {
  display: flex;
  justify-content: space-between;
}

.sb-file-list__icon {
  color: var(--sbt-theme-color-70);
}

.sb-file-list__remove-invalid {
  align-self: center;
  color: var(--sbt-theme-tertiary-color-70);
  font-weight: var(--sbt-font-weight-70);
}

.sb-file-list__file-size {
  font-size: var(--sbt-font-size-20);
  margin-right: var(--sbt-spacing-20);
}

.sb-file-list__actions {
  &-error {
    align-self: center;
  }

  &-loading {
    display: flex;
  }
}
