.sb-form-generator_footer {
  background-color: var(
    --sb-form-generator-footer-background-color,
    var(--sbt-theme-tertiary-color-10)
  );
  border-radius: 0 0 var(--sb-form-generator-footer-border-radius-bottom, 0)
    var(--sb-form-generator-footer-border-radius-bottom, 0);
  border-top: var(--sb-form-generator-footer-border-top-width, 0) solid
    var(--sb-form-generator-footer-border-top-color, transparent);
  bottom: 0;
  padding: var(--sb-form-generator-footer-padding-vertical, 10px)
    var(--sb-form-generator-footer-padding-horizontal, 10px);
  position: sticky;
}
