// --sb-form-generator-repeater-action-width - Action column width

.sb-form-generator_tablerepeater {
  border: 1px solid var(--sbt-theme-tertiary-color-20);
  border-radius: 8px;
  margin: 20px 0;
}

.sb-form-generator_tablerepeater-required {
  color: var(
    --sb-table-repeater-section-required-color,
    var(--sbt-theme-tertiary-color-50)
  );
  font-size: var(--sb-field-label-size, var(--sbt-font-size-25));
  font-weight: var(--sbt-font-weight-40);
  margin-left: auto;
  padding-left: var(--sbt-spacing-20);
}

.sb-form-generator_tablerepeater .sb-form-generator_field {
  margin: var(--sbt-spacing-20) 0;
}

.sb-form-generator_tablerepeater-header {
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-20);
  margin-bottom: var(--sbt-spacing-10);
  padding: var(--sbt-spacing-40);
}

.sb-form-generator_tablerepeater-header_title {
  display: flex;
  justify-content: space-between;
}

.sb-form-generator_tablerepeater-label {
  align-items: center;
  display: flex;
  font-size: var(
    --sb-form-generator-section-header-size,
    var(--sbt-font-size-40)
  );
}

.sb-form-generator_tablerepeater-description {
  font-size: var(
    --sb-form-generator-section-description-size,
    var(--sbt-font-size-25)
  );
}

.sb-form-generator_tablerepeater-wrapper {
  padding: 10px;
}

.sb-form-generator_tablerepeater-table {
  table-layout: fixed;
}

.sb-form-generator_tablerepeater-table_header_actions,
.sb-form-generator_tablerepeater-table_cell_actions {
  text-align: var(--sb-form-generator-repeater-action-alignment, right);
  width: var(--sb-form-generator-repeater-action-width, 120px);
}

.sb-form-generator_tablerepeater-table_header_actions_bulk {
  width: var(--sb-form-generator-repeater-action-width, 160px);
}

.sb-form-generator_key,
.sb-form-generator_tablerepeater-delete {
  margin: var(--sbt-spacing-20) 0;
}

.sb-form-generator_tablerepeater-hidden {
  display: none;
}

.sb-form-generator_tablerepeater-header_actions {
  align-items: top;
  display: flex;
  flex-direction: row;

  > * {
    margin-right: 10px;
    width: 125px;
  }

  .sb-form-generator_field {
    margin: 0;
  }
}
