.sb-form-generator_upload {
  border: 1px solid var(--sbt-theme-tertiary-color-20);
  border-radius: 8px;
  margin: 20px 0;
}

.sb-form-generator_upload-required {
  color: var(--sbt-theme-tertiary-color-50);
  font-size: var(--sb-field-label-size, var(--sbt-font-size-25));
  font-weight: var(--sbt-font-weight-40);
  margin-left: auto;
  padding-left: var(--sbt-spacing-40);
}

.sb-form-generator_upload-header {
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-20);
  margin-bottom: var(--sbt-spacing-10);
  padding: var(--sbt-spacing-40);
}

.sb-form-generator_upload-field-container {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.sb-form-generator_upload-description {
  color: var(
    --sb-form-generator-section-color,
    var(--sbt-theme-tertiary-color-60)
  );
  font-size: var(
    --sb-form-generator-section-description-size,
    var(--sbt-font-size-25)
  );
}
